import { Grid, Tag, Space, Input, Card, Form, Button } from 'antd-mobile';
import { useState } from 'react';
import './ElectricityStyle.scss';

function WaterPage() {
    const [TotalPrice, setTotalPrice] = useState(0);
    const [South, setSouth] = useState({});
    const [North, setNorth] = useState({});

    const sumbit = () => {

        const SouthData = South;//南
        const NorthData = North;//北


        let NDosage = SouthData.newMonth - SouthData.oldMonth; //南用量
        let BDosage = NorthData.newMonth - NorthData.oldMonth; //北用量
        let Total = NDosage + BDosage; //总用量
        let Npercentage = Math.round(NDosage / Total * 10000) / 100.00 + '%'; //南用量百分比
        let Bpercentage = Math.round(BDosage / Total * 10000) / 100.00 + '%'; //北用量百分比
        let NPrice = (TotalPrice / Total) * NDosage; //南用量单价
        let BPrice = (TotalPrice / Total) * BDosage; //北用量单价

        setSouth({ ...South, Dosage: NDosage, percentage: Npercentage, Price: NPrice.toFixed(2) });
        setNorth({ ...North, Dosage: BDosage, percentage: Bpercentage, Price: BPrice.toFixed(2) });
        console.log('South', South);
    }

    return (
        <>
            <div className='Input_style'>
                {/* <Input placeholder='¥ 当月总金额' clearable type='number' onChange={(val) => { setTotalPrice(val) }} /> */}
                <Form layout='horizontal'>
                    <Form.Item label='总费用'>
                        <Input placeholder='¥ 当月总金额' clearable type='number' onChange={(val) => { setTotalPrice(val) }} />
                    </Form.Item>
                </Form>
            </div>
            <div className='tab_view'>
                <Grid columns={2} gap={8}>
                    <Grid.Item>
                        <Card title={
                            <Space>
                                <Tag color='primary' round={true} className='TagStyle'>南</Tag>
                            </Space>}>
                            <>
                                <Form layout='horizontal'>
                                    <Form.Item label='上月'>
                                        <Input placeholder='请输入' type='number' onChange={
                                            (val) => {
                                                setSouth({ ...South, oldMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                    <Form.Item label='本月'>
                                        <Input placeholder='请输入' type='number' onChange={
                                            (val) => {
                                                setSouth({ ...South, newMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                </Form>
                                <div className='info_style'>
                                    <p>用量：{South.Dosage}</p>
                                    <p>占比：{South.percentage}</p>
                                    <p>费用：{South.Price}</p>
                                </div>
                            </>
                        </Card>

                    </Grid.Item>

                    <Grid.Item>
                        <Card title={
                            <Space>
                                <Tag color='success' round={true} className='TagStyle'>北</Tag>
                            </Space>
                        }>
                            <>
                                <Form layout='horizontal'>
                                    <Form.Item label='上月'>
                                        <Input placeholder='请输入' type='number' onChange={
                                            (val) => {
                                                setNorth({ ...North, oldMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                    <Form.Item label='本月'>
                                        <Input placeholder='请输入' type='number' onChange={
                                            (val) => {
                                                setNorth({ ...North, newMonth: Number(val) })
                                            }} />
                                    </Form.Item>
                                </Form>
                                <div className='info_style'>
                                    <p>用量：{North.Dosage}</p>
                                    <p>占比：{North.percentage}</p>
                                    <p>费用：{North.Price}</p>
                                </div>
                            </>
                        </Card>

                    </Grid.Item>
                </Grid>
            </div>
            <div className='btn_style'>
                <Button size='middle' color='primary' onClick={sumbit}>
                    结算
                </Button>
            </div>
        </>
    );
}
export default WaterPage;